/* RomiStyle.css - Version inspirée de ROMI */

/* Variables CSS avec palette de couleurs ROMI */
:root {
    /* Couleurs de ROMI */
    --romi-orange: #FF6B00;     /* Corps de ROMI */
    --romi-blue: #00B4D8;       /* T-shirt de ROMI */
    --romi-red-orange: #FF4500; /* Salopette de ROMI */
    --romi-black: #1C1C1C;      /* Cheveux de ROMI */
    --romi-gray: #4A4A4A;       /* Détails de ROMI */

    /* Conservation des variables existantes avec quelques ajustements */
    --primary-color: var(--romi-red-orange);
    --secondary-color: var(--romi-black);
    --accent-blue: var(--romi-blue);
    --accent-green: #4CAF50;
    --accent-orange: #FF9800;
    --accent-purple: #9C27B0;
    --accent-yellow: #FFCC22;
    --background-light: linear-gradient(135deg, rgba(0,180,216,0.05) 0%, rgba(255,107,0,0.05) 100%);
    --white: #ffffff;
    
    /* Ajustements des ombres et transitions */
    --shadow-sm: 0 3px 6px rgba(0,180,216,0.1);
    --shadow-md: 0 6px 12px rgba(0,180,216,0.2);
    --border-radius-sm: 5px;
    --border-radius-md: 10px;
    --border-radius-full: 50%;
    --transition-default: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

/* Reset et styles de base */
.romi-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    min-height: 100vh;
    background: var(--background-light);
    font-family: 'Comic Sans MS', 'Arial Rounded MT Bold', sans-serif;
}

/* Animations inspirées de ROMI */
@keyframes romi-pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.05); }
}

@keyframes romi-wiggle {
    0%, 100% { transform: rotate(0deg); }
    25% { transform: rotate(-3deg); }
    75% { transform: rotate(3deg); }
}

@keyframes pulse {
    0% { transform: scale(1); box-shadow: 0 0 0 0 rgba(255, 68, 34, 0.4); }
    70% { transform: scale(1.05); box-shadow: 0 0 0 10px rgba(255, 68, 34, 0); }
    100% { transform: scale(1); box-shadow: 0 0 0 0 rgba(255, 68, 34, 0); }
}
/* En-tête */
.romi-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    padding: 10px 20px;
    background-color: var(--white);
    border-radius: var(--border-radius-md);
    box-shadow: var(--shadow-sm);
}

.romi-header-left {
    display: flex;
    align-items: center;
    gap: 20px;
}

.romi-character {
    width: 60px;
    height: 60px;
    object-fit: contain;
}

.romi-welcome {
    font-size: 24px;
    margin: 0;
    color: var(--romi-black);
}

/* Zone principale */
.romi-main {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

/* Panneau des thèmes */
.romi-themes-panel {
    width: 100%;
    margin-bottom: 10px;
}

.theme-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 100%;
}

.theme-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}

/* Boutons d'action circulaires */
.action-buttons-circle {
    display: flex;
    gap: 15px;
    align-items: center;
    margin-bottom: 15px;
}

.circle-button {
    width: 60px;
    height: 60px;
    border-radius: var(--border-radius-full);
    border: 3px solid var(--romi-blue);
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: var(--transition-default);
    color: var(--white);
    padding: 0;
    margin: 0;
    background-color: var(--romi-blue);
    box-shadow: var(--shadow-sm);
}

.circle-button:hover:not(:disabled) {
    transform: translateY(-5px) rotate(5deg);
    box-shadow: var(--shadow-md);
    animation: romi-wiggle 0.3s ease-in-out;
}

.circle-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.circle-button.active {
    animation: romi-pulse 1s infinite;
}

/* Styles spécifiques des boutons d'action */
.circle-button.bluetooth {
    background-color: var(--romi-blue);
}

.circle-button.bluetooth.connected {
    background-color: var(--romi-red-orange);
    animation: pulse 2s infinite;
}

.circle-button.game {
    background-color: var(--accent-green);
}

.circle-button.game.active {
    animation: pulse 2s infinite;
}

.circle-button.presentation {
    background-color: var(--accent-orange);
}

.circle-button.presentation.active {
    animation: pulse 2s infinite;
}

.circle-button.music {
    background-color: var(--accent-purple);
    transition: all 0.3s ease;
}

.circle-button.music:hover {
    transform: scale(1.05) rotate(15deg);
    background-color: #7B1FA2;
}

.circle-button.music.active {
    background-color: var(--accent-green);
    animation: pulse 2s infinite;
}

.circle-button.back {
    background-color: var(--accent-orange);
    font-size: 20px;
}

.circle-button.back:hover {
    transform: scale(1.05) rotate(-15deg);
}
/* Layout principal */
.romi-content-wrapper {
    display: flex;
    width: 100%;
    gap: 20px;
    justify-content: center;
    align-items: flex-start;
}

/* Grille de boutons personnalisée */
.romi-buttons-grid-custom {
    position: relative;
    width: 600px;
    height: 600px;
    margin: 0;
    padding: 10px;
    background-color: var(--white);
    border-radius: var(--border-radius-md);
    box-shadow: var(--shadow-sm);
    border: 3px solid var(--romi-blue);
    flex-shrink: 0;
}

/* Boutons ronds */
.romi-button-round {
    position: absolute;
    width: 85px;
    height: 85px;
    border-radius: var(--border-radius-full);
    font-family: Arial, sans-serif;
    color: black;
    border: 3px solid var(--romi-black);
    background-color: var(--romi-blue);
    padding: 5px;
    margin: 0;
    cursor: pointer;
    transition: var(--transition-default);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: bold;
    transform: translate(-50%, -50%);
    box-shadow: var(--shadow-sm);
}

.romi-button-round span {
    width: 100%;
    display: block;
    word-wrap: break-word;
    line-height: 1.2;
}

/* Positions des boutons */
.romi-button-round:nth-child(1) { top: 50%; left: 50%; }
.romi-button-round:nth-child(2) { top: 30%; left: 50%; }
.romi-button-round:nth-child(3) { top: 35.75%; left: 64.25%; }
.romi-button-round:nth-child(4) { top: 50%; left: 70%; }
.romi-button-round:nth-child(5) { top: 64.25%; left: 64.25%; }
.romi-button-round:nth-child(6) { top: 70%; left: 50%; }
.romi-button-round:nth-child(7) { top: 64.25%; left: 35.75%; }
.romi-button-round:nth-child(8) { top: 50%; left: 30%; }
.romi-button-round:nth-child(9) { top: 35.75%; left: 35.75%; }
.romi-button-round:nth-child(10) { top: 35.75%; left: 84.25%; }
.romi-button-round:nth-child(11) { top: 50%; left: 90%; }
.romi-button-round:nth-child(12) { top: 64.25%; left: 84.25%; }
.romi-button-round:nth-child(13) { top: 64.25%; left: 15.75%; }
.romi-button-round:nth-child(14) { top: 50%; left: 10%; }
.romi-button-round:nth-child(15) { top: 35.75%; left: 15.75%; }

/* Styles des boutons en mode musique */
.romi-button-round.music-mode {
    transition: all 0.4s ease;
}

.romi-button-round.music-mode:hover:not(:disabled) {
    transform: translate(-50%, -50%) scale(1.1);
    box-shadow: 0 0 20px rgba(0,180,216,0.3);
 
}

/* Styles spécifiques pour chaque instrument */
.romi-button-round.instrument-handpan {
    background-color: var(--romi-orange);
    border-color: var(--romi-black);
}

.romi-button-round.instrument-celesta {
    background-color: var(--romi-blue);
    border-color: var(--romi-black);
}

.romi-button-round.instrument-pianostars {
    background-color: var(--romi-red-orange);
    border-color: var(--romi-black);
}

.romi-button-round.instrument-looper {
    background-color: var(--romi-black);
    color: white;
    border-color: var(--romi-gray);
}

/* États des boutons */
.romi-button-round:hover:not(:disabled) {
    transform: translate(-50%, -50%) scale(1.05);
    box-shadow: var(--shadow-md);
   
}

.romi-button-round.empty-slot {
    background-color: #e0e0e0 !important;
    cursor: default;
    border-color: #ccc;
    color: #999;
}

.romi-button-round.found {
    opacity: 0.5;
    cursor: not-allowed;
    background-color: #ccc !important;
}

.romi-button-round.pressed {
    transform: translate(-50%, -50%) scale(0.95);
    box-shadow: inset 0 0 10px rgba(0,0,0,0.2);
}

/* Zone vidéo et compteur d'erreurs */
.romi-preview {
    flex: 1;
    max-width: 600px;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.video-container {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
    background: black;
    border-radius: var(--border-radius-md);
    overflow: hidden;
    box-shadow: var(--shadow-sm);
    border: 3px solid var(--romi-blue);
}

.preview-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

/* Loading indicator */
.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--white);
    background: rgba(0, 0, 0, 0.7);
    padding: 10px 20px;
    border-radius: var(--border-radius-sm);
    font-size: 16px;
}
/* Compteur d'erreurs */
.error-counter-frame {
    width: 100%;
    padding: 20px;
    background-color: rgba(255,107,0,0.05);
    border: 2px solid var(--romi-blue);
    border-radius: 15px;
    box-shadow: var(--shadow-sm);
    transition: var(--transition-default);
}

.error-counter-content {
    text-align: center;
}

.error-counter-content h3 {
    color: var(--romi-black);
    margin: 0 0 15px 0;
    font-size: 20px;
    font-weight: bold;
}

.error-counter-content p {
    margin: 10px 0;
    font-size: 16px;
    color: var(--romi-gray);
}

.error-count {
    font-size: 28px;
    font-weight: bold;
    color: var(--romi-red-orange);
    margin: 0 5px;
}

.error-message {
    margin-top: 15px;
    padding: 10px;
    border-radius: var(--border-radius-sm);
    background-color: rgba(0,180,216,0.1);
    font-style: italic;
    color: var(--romi-black);
}

/* Thèmes de boutons */
.romi-theme-button {
    background-color: var(--romi-blue);
    color: white;
    border-radius: 20px;
    transition: var(--transition-default);
    border: none;
    padding: 10px 15px;
}

.romi-theme-button:hover {
    transform: scale(1.05);
    box-shadow: var(--shadow-md);
}

.romi-theme-button.active {
    background-color: var(--romi-red-orange);
    animation: romi-pulse 1s infinite;
}
/* Media Queries */
@media (max-width: 1200px) {
    .romi-content-wrapper {
        flex-direction: column;
        align-items: center;
    }

    .romi-buttons-grid-custom {
        width: 550px;
        height: 550px;
        border: 2px solid var(--romi-blue);
    }

    .romi-button-round {
        width: 75px;
        height: 75px;
    }

    .romi-preview {
        max-width: 800px;
        width: 100%;
        margin-top: 20px;
    }
}

@media (max-width: 768px) {
    .theme-buttons {
        flex-direction: column;
        align-items: center;
    }

    .action-buttons-circle {
        flex-wrap: wrap;
        justify-content: center;
    }

    .circle-button {
        width: 50px;
        height: 50px;
        font-size: 20px;
    }

    .romi-buttons-grid-custom {
        width: 450px;
        height: 450px;
    }

    .romi-button-round {
        width: 65px;
        height: 65px;
    }
}

@media (max-width: 480px) {
    .romi-welcome {
        font-size: 20px;
    }

    .romi-character {
        width: 40px;
        height: 40px;
    }

    .circle-button {
        width: 40px;
        height: 40px;
        font-size: 16px;
    }

    .romi-buttons-grid-custom {
        width: 350px;
        height: 350px;
    }

    .romi-button-round {
        width: 55px;
        height: 55px;
        font-size: 9px;
        border-width: 2px;
    }
}